___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-family: "Black Ops One", sans-serif;
  --font-color: white;
}

html {
  background: #4fcaff;
  background: radial-gradient(
    circle,
    rgba(79, 202, 255, 1) 0%,
    rgba(0, 145, 255, 1) 52%,
    rgba(0, 75, 214, 1) 100%
  );
}

button {
  cursor: pointer;
}

.main-container h1 {
  font-size: 4rem;
  text-align: center;
  color: var(--font-color);
  letter-spacing: 1px;
  font-weight: lighter;
  text-transform: uppercase;
}

.main-container {
  min-height: 100vh;
}

#message {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: #222;
  color: #fff;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 1rem;
  z-index: 1001;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

#winning-message {
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  background: #222;
  color: yellow;
  padding: 30px;
  border-radius: 8px;
  font-size: 2rem;
  z-index: 1001;
}

#winning-message button {
  font-size: 1.5rem;
  margin-top: 20px;
  padding: 10px;
  border: none;
  border-radius: 8px;
  font-family: "Black Ops One", sans-serif;
  color: white;
  background-color: #4fcaff;
}

#ship-select {
  appearance: none; /* Remove default styling */
  -webkit-appearance: none;
  -moz-appearance: none;

  background-color: #007acc;
  border: 1px solid #444;
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 1rem;
  font-family: inherit;
  color: white;
  cursor: pointer;
  transition: border-color 0.2s, box-shadow 0.2s;

  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 16px 16px;
  padding-right: 36px; /* Make space for the arrow */
}

#ship-select:hover {
  background-color: #005fa3;
}

#ship-select:focus {
  outline: none;
  border-color: #007acc;
  box-shadow: 0 0 4px rgba(0, 122, 204, 0.6);
}

.setup-controls button {
  background-color: #007acc;
  width: 200px;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 1rem;
  font-family: inherit;
  cursor: pointer;
  transition: background-color 0.2s, box-shadow 0.2s;
}

.setup-controls button:hover {
  background-color: #005fa3;
}

.setup-controls button:focus {
  outline: none;
  box-shadow: 0 0 4px rgba(0, 122, 204, 0.6);
}

@media screen and (max-width: 768px) {
  .main-container h1 {
    font-size: 3rem;
  }
}
